<template>
  <section style="position: relative;" >
    <Scrollable x class="scrollable product-list" :class="size" ref="product-list"
      @scroll='updateScrollable'>
      <row>
        <div
          ref="product-item"
          class="item"
          v-for="(item,index) in items" :key="item.url"
          :class="{current: index === current}"
          @click="$emit('clickItem', index)">
          <video v-if="item.type === 'video'"
            :src="item.url"
            style="width: 56px;height: 56px;" />

          <BaseImage v-else fit="cover" :src="item.url">
          </BaseImage>

        </div>
      </row>
    </Scrollable>
    <div class="scroll-left" @click="scrollRow(1, 1)" v-if="canScrollLeft">
      <i class="el-icon-arrow-left" style="font-size: 26px;"></i>
    </div>
    <div class="scroll-right" @click="scrollRow(0, 1)" v-if="canScrollRight">
      <i class="el-icon-arrow-right" style="font-size: 26px;"></i>
    </div>
  </section>

</template>
<script>
import Scrollable from '@/components/Scrollable';
import BaseImage from '@/components/base/Image';

export default {
  components: {
    BaseImage,
    Scrollable,
  },
  props: {
    size: {

    },
    current: {
      type: Number,
      default: 0,
    },
    items: {
      type: Array,
      default: () => ([]),
    },
  },
  data() {
    return {
      canScrollLeft: null,
      canScrollRight: null,
    };
  },
  watch: {
    items() {
      const target = this.$refs['product-list'].$el.children[0];

      // 等渲染完成，才计算是否显示scorll参数
      setTimeout(() => {
        this.updateScrollable({ target });
      }, 500);
    },
  },
  // async mounted() {
  //   await this.$nextTick();
  //   debugger;
  //   const target = this.$refs['product-list'].$el.children[0];

  //   // 等渲染完成，才计算是否显示scorll参数
  //   setTimeout(() => {
  //     this.updateScrollable({ target });
  //   }, 500);
  // },
  methods: {
    async scrollRow(dir = 0 /** num = 2* */) {
      // eslint-disable-next-line no-unused-vars
      const target = this.$refs['product-list'].$el.children[0];
      if (!target.scrollTo) {
        if (!dir) {
          target.scrollLeft += target.clientWidth;
        } else {
          target.scrollLeft -= target.clientWidth;
        }
        return;
      }
      if (!dir) {
        target.scrollTo({
          left: target.scrollLeft + target.clientWidth,
          behavior: 'smooth',
        });
      } else {
        target.scrollTo({
          left: target.scrollLeft - target.clientWidth,
          behavior: 'smooth',
        });
      }
    },
    updateScrollable({ target }) {
      /** @type {HTMLDivElement} */
      this.canScrollLeft = target.scrollLeft !== 0;
      this.canScrollRight = target.scrollLeft + target.clientWidth !== target.scrollWidth;
    },
  },
};

</script>
