    border-radius:4px;
    border: 1px solid #ECECEC;
    padding: 1px;
    transition: border-color 0.3s;
    width: 60px;
    height: 60px;
    flex: none;
    cursor: pointer;
    + .item {
      margin-left: 10px;
    }
    &:hover {
      border-color: $color-primary;
    }
    &.current,
    &:active {
      border-color: $color-primary;
      border-width: 2px;
      padding: 0;
      // padding-bottom: 10px;
    }
  }
  // &.sm {
  //   line-height: 20px;
  //   height: 182px;
  //   /deep/ .item {
  //     width: 202px;
  //     height: 174px;
  //     center {
